.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.85);
  z-index: 999;
}

.modalBody {
  background-color: #fff;
  position: fixed;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  /* padding: 0 20px 0 20px; */
  overflow-y: scroll; /* has to be scroll, not auto */
  -webkit-overflow-scrolling: touch;
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

.exitButton {
	border: none;
	cursor: pointer;
	outline: none;
  background: none;
  padding: 0;
}

.modalHeader {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 7px 15px 7px 25px;
  background-color: black;
}
.modalMain {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.modalTitle, .clearIcon {
  color: #fff;
}

@media screen and (min-width: 450px) {
  .modalBody {
    width: auto;
    height: auto;
    border-radius: 8px;
  }
  .modalHeader {
    padding: 10px 0px 10px 0px;
  }

};



