.menuComponent {
  position: fixed;
  bottom: 0;
  height: calc(100vh - 40px);
  width: 250px;
  border-right: 1px solid black;
  background-color: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  /* align-items: center; */
  z-index: 4;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}



