@import url('https://fonts.googleapis.com/css?family=Roboto');

.roomsComponent {
	width: 100%;
}

.roomsComponent > ul {
	list-style-type: none;
	padding: 0;
	margin: 0;
}

.subscribedRoom {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	margin-top: 10px;
}

.roomNameButton {
	cursor: pointer;
	outline: none;
	height: 55px;
	text-align: left;
	background: none;
	width: 100%;
	border-style: none;
	/* border: 1px solid black; */
	margin-top: 10px;
	border-radius: 4px;
	padding: 0;
	margin: 0;
	font-size: unset;
}

.roomNameButton > div {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: start;
	    justify-content: flex-start;
}

.roomName {
	font-family: 'Roboto', sans-serif;
	margin-left: 10px;
	font-size: 20px;
}

@media screen and (min-width: 450px) {
  .subscribedRoom {
		height: 35px;
	}

	.roomNameButton {
		height: 25px;
		border-radius: 0px;
		border: 0;
	}
};



